@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body {
background-color: #15181C;
  background: linear-gradient(
    43deg,
    #15181C 0%,
    #21252B 46%,
    #383E4A 100%
  ) no-repeat fixed  ;
  background-size: cover;

  margin: 0px;
  font-family: "Fira Code", monospace;

  overflow: hidden;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: none;

}
a {
  color: white;
}
body::selection {
  color: none;
  background: none;
}
#root{
  display: inline-block;

  pointer-events: none;
}
h1{
    text-align: center;
    padding: 10px;
}


.folder-icon {
  width: 50px;
  margin: 0px auto 0px 5px;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  pointer-events: auto;
}
.folder-icon:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
  
}
.file-icon {
  width: 40px;
  margin: 0px auto 10px 10px;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  pointer-events: auto;
}
.file-icon:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}
#icon-name {
  position: absolute;
  top: 35px;
  left:15px;

  color: white;
  font-size: 0.8rem;
  display: inline-block;
  width: 80px;
 
}
.container {
  margin:15px 5px;
  padding: 5px;
  display: inline-block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.window-position {
  width: 700px;
  height: 400px;
  position: absolute;
  bottom: 100px;
  left: 250px;
  z-index: 2;
  pointer-events: auto;
  font-size: 0.8rem;
  justify-content: center;

  
}
.page-window {
 position: relative;
  padding: 0px 0px 20px 0px;
  height: 450px;
  width: 700px;
  background-color: #282c34;
  border-radius: 5px;
  color: white;
 box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
  overflow: auto;
  text-align: justify;
  z-index: -1;
}
.handle{
  width: 100%;
  position: fixed;
}
footer{
  height: 100px;
}
::-webkit-scrollbar {
  width: 15px;

}
.page-window h1{
  margin-top: 20px;
  font-size: 1.5rem;
  color: #b498e7;
}
p{
  color: whitesmoke;
}
h4{
  color: rgb(218, 218, 140);
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 

::-webkit-scrollbar-thumb {
  background: rgb(99, 99, 155); 
  border-radius: 10px;
}


::-webkit-scrollbar-thumb:hover {
  background: rgb(73, 83, 110); 
}
@media screen and (max-width: 500px) {
  .window-position{
    width: 30%;
    height: 30%;
  }
}
.dot {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  display: inline-block;
  margin: 2px;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  border: 1px solid ;
}
.dot:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}
.window {
 width: 100%;
 position: absolute;
  border-radius: 3px;
  margin-bottom: 5px;
  background-color: grey;
   z-index: 100 !important; 

}
.card {
 margin: 5%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 90%;
  text-align: center;
  font-size: 1rem;
  background-color: #181818;
  border-radius: 20px;
 
}
#status{
  display: inline;
}
.card a{
  color: #fff;
  font:1rem;
  text-transform: uppercase;
}
.card:hover {
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.container {
  padding: 2px 16px;
}
.card img {
  padding: 10px;
  width: 100px;
  float: center;
  
}
.aboutme-container {
  font-size: 1rem;
  padding: 20px 50px 20px 50px;

}
.aboutme-container h1 {

font-size: 2rem;
}
.aboutme-container img {
  float: right;
  width: 200px;
}

input,
select,
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid gainsboro;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
 background:#0D1117;
}

.contact-container {
  text-align: left;
  border-radius: 5px;
  background-color: #21252b;
  margin: 40px 0px 0px 0px;
  padding: 15px;
  
}
.contact-container button {
  border-radius: 10px;
  background-color: #0082ff;
  padding: 10px;
  box-shadow: rgb(0, 0, 0) 0px 20px 30px -10px;
  font-family: "Fira Code", monospace;
}
.contact-container button{
  margin: 0px 10px;
  
  transition: -webkit-transform 0.1s;
  
  transition: transform 0.1s;
  
  transition: transform 0.1s, -webkit-transform 0.1s;
}
.contact-container button:hover {
-webkit-transform: scale(1.3);
        transform: scale(1.3);
}


.aboutme-container table{
  min-width: 300px;
}
.aboutme-container th{
  font-size: 1.3rem;
  color: rgb(236, 255, 127);
}
.aboutme-container td{
  color: #4FAFEF;
}
progress {
  border-radius: 5px; 
  width: 150px;
  height: 15px;
  border:solid black;
  box-shadow: 1px 1px 4px rgba( 0, 0, 0, 0.5 );
  

}

progress::-webkit-progress-bar {
  background-color: #1D1E22;
  border-radius: 5px 0px 0px;
  
}
.green::-webkit-progress-value{
  background-color: #97E263;

}
.blue::-webkit-progress-value{
  background-color: #528BFF;

}
.red::-webkit-progress-value{
  background-color: #FF5252;

}
.pink::-webkit-progress-value{
  background-color: #C678DD;

}
.aboutme-container td{
  transition: -webkit-transform 0.1s;
  transition: transform 0.1s;
  transition: transform 0.1s, -webkit-transform 0.1s;

}
.aboutme-container td:hover{
 -webkit-transform: scale(1.2);
         transform: scale(1.2);

}
.aboutme-container td img{
  width: 10px;
}

.taskbar{
    width: 100vw;
    height: 20px;
    background-color: rgb(255, 255, 255);
