  .circle {
   display: none;
  }
  
}
.spinner {
  width: 300px;
  position: absolute;
  left: 40vw;
  top: 40vh;
  background-color: transparent;
  color: white;
  justify-content: center;
  text-align: center;
  font-size:1rem;
  display: flex;

}

.spinner span {
    position: relative;
    top: 70px;
  font-size: 2rem;
  padding-right: 1rem;

}

.half-spinner {
  width: 50px;
  height: 50px;
  border: 3px solid #03fc4e;
  border-top: 3px solid transparent;
  border-radius: 50%;
  -webkit-animation: spin 0.5s linear 0s infinite;
          animation: spin 0.5s linear 0s infinite;
  position: fixed;
}

.completed {
  font-size: 2rem;
  color: #03fc4e;

  -webkit-animation: bigger 1s linear;

          animation: bigger 1s linear;
}

@-webkit-keyframes bigger {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(2);
            transform: scale(2);
  }
}

@keyframes bigger {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(2);
            transform: scale(2);
  }
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



